.wetherBg {
  background: url("https://media.nationalgeographic.org/assets/photos/000/263/26383.jpg");
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.wetherResultBox {
  margin: auto;
  width: 500px;
  height: 300px;
}
.weatherIcon {
  margin-top: 2rem;
  width: 100px;
  height: 100px;
}
.weatherCity {
  font-size: 35px;
  color: white;
}
.weatherTemp {
  font-size: 55px;
  font-weight: 700;
  color: rgb(173, 24, 24);
}
h1 {
  color: aqua;
  font-weight: 700;
}
